import React, { useState, useEffect } from "react";
import '../css/styles.css';
import UserService from "../services/user.service";

function Home() {
  const [content, setContent] = useState("");

  useEffect(() => {
    UserService.getPublicContent().then(
      (response) => {
        setContent(response.data);
      },
      (error) => {
        const data =
          (error.response && error.response.data) ||
          error.message ||
          error.toString();

        setContent(data);
      }
    );
  }, []);

  return (
    <div className="container">
      
      <img src="../healthcare-medical-concept-med.png"/>
   <div />
   <div id="emilebilir" className="emilebilir">

    <div id="EMLEBLR_STRLER">
     <span>EMLEBLR<br />STRLER</span>
    </div>
   </div>
   <div id="milmeyen" className="milmeyen">

    <div id="EMLMEYEN_STRLER">
     <span>EMLMEYEN<br />STRLER</span>
    </div>
   </div>
   <div id="Component_7__1" className="Component_7___1">

    <div id="EMLEBLR_HEMOSTAT">
     <span>EMLEBLR<br />HEMOSTAT</span>
    </div>
   </div>
   <div id="Component_8__1" className="Component_8___1">

    <div id="DENTAL_STRLER">
     <span>DENTAL<br />STRLER</span>
    </div>
   </div>
   <div id="Component_9__1" className="Component_9___1">

    <div id="VETERNERLK">
     <span>VETERNERLK</span>
    </div>
   </div>
    </div>
  );
};

export default Home;